import Vue from 'vue'
import Router from 'vue-router'
import Oneday from '@/components/Oneday'
import TaiwanWeather from '@/components/TaiwanWeather'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TaiwanWeather',
      component: TaiwanWeather
    },
    {
      path: '/Oneday',
      name: 'Oneday',
      component: Oneday
    },
  ]
})



